@import url(https://fonts.googleapis.com/css?family=Lobster|Montserrat:300,400,500,700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #F5F5F5;
  font-family: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}
/* HEADER */

.header {
  width: 100%;
  background: #fff;
  border-radius: 0 0 25px 25px;
  box-shadow: 5px 5px 10px #ddd;
  position: absolute;
  top: 0;
  z-index: 2;
}

.header-content {
  width: 90%;
  margin: auto;
  padding: 20px 0;
  z-index: 2;
}

.header-content::after {
  content: '';
  display: block;
  clear: both;
}

.header-content .logo {
  font-family: 'Lobster', cursive;
  font-size: 50px;
  display: block;
  text-decoration: none;
  float: left;
  color: #494843;
  margin-left: 16px;
}

.header-content .logo span {
  padding-left: 12px;
}

.header-content .logo i {
  font-size: 40px;
}

.header-content .search_user {
  font-size: 24px;
  color: #494843;
  float: right;
  margin-right: 16px;
  line-height: 60px;
}

.header-content .search_user > * {
  padding: 0 16px;
}

.header-content .search_user .search {
  border-right: 1px solid #494843;
}

@media (max-width: 600px) {
  .header-content .logo {
    font-size: 30px;
  }
  .header-content .logo i {
    font-size: 25px;
  }
  .header-content .search_user {
    font-size: 15px;
    line-height: 40px;
  }
}
.recipes-wrapper {
  width: 100%;
  text-align: center;
  background: #F5F5F5;
  position: relative;
  height: 100vh;
}

.orange {
  color: #fd6721;
}

.recipes-wrapper > h2 {
  padding-top: 140px;
  padding-bottom: 20px;
  color: #fd6721;
  font-size: 40px;
  position: relative;
  z-index: 2;
}

.recipes {
  width: 90%;
  text-align: center;
  margin: auto;
  position: relative;
  z-index: 2;
}

.recipes::after {
  content: "";
  display: block;
  clear: both;
}

.recipe {
  margin: 16px;
  background: #fff;
  padding: 25px;
  border-radius: 25px;
  box-shadow: 5px 5px 10px #ddd;
}

.recipe .photo {
  width: 100%;
  padding-bottom: 70%;
  border-radius: 25px;
  display: inline-block;
  background-size: cover!important;
  background-repeat: no-repeat!important;
  background-position: center!important;
}

.recipe .title {
  text-align: left;
  font-weight: bold;
  color: #494843;
  padding-top: 16px;
  padding-bottom: 16px;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.recipe .title a {
  color: #494843;
}

.recipe .description {
  text-align: left;
  color: #666;
  font-size: 15px;
  height: 60px;
  margin-bottom: 16px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.recipe .details {
  text-align: left;
  color: #9b9b9b;
  border-top: 1px solid #ddd;
  padding-top: 16px;
}

.recipe .details::after {
  content: "";
  display: block;
  clear: both;
}

.recipe .readyInMinutes, .recipe .servings {
  float: left;
}

.recipe .servings {
  padding-left: 10px
}

.lets-cook-btn {
  display: inline-block;
  text-decoration: none;
  background: #fd6721;
  color: #fff;
  padding: 0.3rem 0.8rem;
  margin-top: 1.5rem;
  letter-spacing: 1px;
  font-size: 14px;
}

/* SLICK */

.slick-dots li button:before {
  font-size: 8px!important;
}

.slick-dots li.slick-active button:before {
  color: #fd6721!important;
}

.slick-prev:before, .slick-next:before {
  font-size: 30px!important;
  color: #333!important;
}

/* LOADER */

.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, #ffffff), color-stop(42%, rgba(255, 255, 255, 0)));
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #ffffff;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background: #f5f5f5;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@media (max-width: 600px) {
  .recipes-wrapper > h2 {
    font-size: 30px;
  }

  .recipes {
    width: 85%;
  }
}
.single-recipe-wrapper {
  width: 100%;
  padding-top: 16vh;
  height: 100vh;
  background: #F5F5F5;
}

.single-recipe {
  width: 90%;
  margin: auto;
  padding: 2rem 3rem;
  height: 80vh;
  background: rgba(255, 255, 255, 0.9);
  background: #fff;
  border-radius: 25px;
  position: relative;
  z-index: 2;
  overflow-y: scroll;
}

.single-recipe .recipe-title {
  font-size: 30px;
  padding-bottom: 16px;
}

.single-recipe .photo {
  width: 100%;
  padding-bottom: 70%;
  border-radius: 25px;
  margin-bottom: 20px;
  background-position: center!important;
  background-size: cover!important;
  background-repeat: no-repeat!important;
}

.description {
  text-align: left;
  color: #666;
  font-size: 15px;
}

.description > h2 {
  color: #000;
  padding-bottom: 8px;
}

.left-side {
  float: left;
  width: 35%;
}

.right-side {
  float: left;
  width: 65%;
  padding-left: 2rem;
}
.right-side-section .instructions-title, .right-side-section .ingredients-title {
  padding-bottom: 8px;
}

.right-side-section .instructions-title {
  padding-top: 24px;
}

.ingredients {
  color: #666666;
  font-size: 15px;
}

.ingredients > p, .instructions >p {
  padding: 2px 0;
}

.instructions {
  color: #666666;
  font-size: 15px;
}

@media (max-width: 650px) {
  .single-recipe-wrapper {
    padding-top: 12vh;
  }

  .single-recipe {
    width: 95%;
    margin: auto;
    padding: 1rem 1.5rem;
    height: 85vh;
  }
  .left-side {
    float: none;
    width: 100%;
  }
  .right-side {
    float: none;
    width: 100%;
    padding-top: 1rem;
    padding-left: 0;
    padding-right: 0;
  }
  .recipe-title {
    font-size: 22px!important;
  }
  .ingredients-title, .instructions-title {
    font-size: 18px;
  }
  .ingredients, .instructions {
    font-size: 14px;
  }
}
